.project {
    list-style: none;
    border-bottom: 1px solid #E8E8E8;
    ;
    padding: 20px 0;
    transition: all 0.2s ease-in-out;
  }
  
  @media (max-width: 768px) {
    .project {
      border-bottom: 1px solid #e8e8e8;
    }
  }
  
  .project:last-child {
    border-bottom: none;
    padding-bottom: 0;
  }
  
  .project:first-child {
    padding-top: 0;
  }
  
  .project__link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 30px;
    font-weight: 400;
    line-height: 1.66;
    color: #000;
    text-decoration: none;
    letter-spacing: -0.04em;
    transition: all 0.2s ease-in-out;
  }
  
  .project__link:hover {
    color: #3456F3;
  }
  
  .project__link:active {
    transform: scale(0.99);
  }
  
  @media (max-width: 768px) {
    .project__link {
      font-size: 28px;
      line-height: 1.78;
    }
  }
  
  @media (max-width: 590px) {
    .project__link {
      font-size: 18px;
      line-height: 1.55;
    }
  }
  
  .project__icon {
    transition: all 0.2s ease-in-out;
  }
  
  @media (max-width: 590px) {
    .project__icon {
      width: 10px;
      height: 10px;
    }
  }
  
  .project:hover .project__icon {
    transform: scale(1.2);
  }