.techs {
    padding: 105px 0 102px;
    background-color: #F5F5F5;
}
@media (max-width: 490px) {
    .techs{
      background: none;
    }
}
  
  @media (max-width: 1024px) {
    .techs {
      padding: 94px 0;
    }
  }
  
  @media (max-width: 500px) {
    .techs {
      padding: 77px 0;
    }
  }
  
  .techs__texts {
    text-align: center;
    padding-top: 90px;
    margin-bottom: 100px;
    letter-spacing: -0.04em;
  }
  
  @media (max-width: 1024px) {
    .techs__texts {
      padding-top: 80px;
      margin-bottom: 83px;
    }
  }
  
  @media (max-width: 500px) {
    .techs__texts {
      padding-top: 58px;
      margin-bottom: 50px;
    }
  }
  
  .techs__title {
    margin: 0;
    font-size: 50px;
    font-weight: 400;
    line-height: 1.16;
    margin-bottom: 26px;
    letter-spacing: -0.04em;
  }
  
  @media (max-width: 1024px) {
    .techs__title {
      margin-bottom: 22px;
    }
  }
  
  @media (max-width: 500px) {
    .techs__title {
      font-size: 30px;
      line-height: 1.21;
      margin-bottom: 24px;
    }
  }
  
  .techs__description {
    margin: auto;
    max-width: 460px;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.42;
  }
  
  @media (max-width: 1024px) {
    .techs__description {
      font-size: 12px;
      line-height: 1.5;
      letter-spacing: -0.04em;
    }
  }
  
  @media (max-width: 500px) {
    .techs__description {
      font-size: 11px;
      line-height: 1.45;
    }
  }
  
  .techs__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, 90px);
    justify-content: center;
    gap: 10px;
    margin: 0 auto;
    padding: 0;
  }
  
  @media (max-width: 1024px) {
    .techs__list {
      max-width: 648px;
      grid-template-columns: repeat(auto-fit, 87px);
      gap: 6px;
    }
  }
  
  @media (max-width: 500px) {
    .techs__list {
      width: 200px;
      gap: 10px;
    }
  }